<template>
     <transition name="fade">
        <ul class="nav-list" id="nav-bar-nav">
            <li class="nav-item" v-for="(item,index) in items">
                <router-link :to="'/' + item.ad">{{item.name}}</router-link>
            </li>
        </ul>
    </transition>
</template>

<script>
    import { mapActions } from 'vuex';
    export default {
        data () {
            return {
                items: [
                    {name:'技术博客',ad:'personBlog'},
                    {name:'AngularJs',ad:'angular'},
                    {name:'NodeJs',ad:'nodejs'},
                    {name:'VueJs & React',ad:'vuereact'},
                    {name:'Javascript',ad:'javascript'},
                    {name:'移动端开发',ad:'mobile'},
                    {name:'构建工具',ad:'maketool'},
                    {name:'工具手册',ad:'handbook'},
                    {name:'静态资源',ad:'quietResource'},
                    {name:'版本控制',ad:'gits'},
                    {name:'关于本站',ad:'aboutme'}
                ]
            }
        },
        methods: {
            btnNone () {
                // this.$emit('btnNone')
                // return false;
            }
        }
    }
</script>

<style scoped>
    .nav-list{
        width: 200px;
        position: absolute;;
        top: 80px;
        left: 20px;
        margin-top: 20px;
        border-top: 1px solid #1BA265;
    }
    .nav-item{
        list-style: none;
        height: 40px;
        line-height: 40px;
        position: relative;
        text-align: center;
        cursor: pointer;
        margin-bottom: 4px;
        border-bottom: 1px solid #eee;
    }
    .nav-item a{
        display: block;
        text-decoration: none;
        color: #333;
    }
    .nav-item a:hover{
        background-color: #10A0EA;
        cursor: pointer;
        color: #fff;
        font-weight: bold;
    }
    a.router-link-active{
        color:#10A0EA;
        font-weight: bold;
    }
    a.router-link-active:before{
        position: absolute;
        left: 0;
        bottom: 0;
        display: block;
        content: '';
        width: 4px;
        height: 40px;
        background-color: #10A0EA;
    }
    @media screen and (max-width: 1080px){
        #nav-bar-nav{
            position: absolute;
            top: 20px;
            left: 0;
            width: 100%;
            /*padding: 0;*/
            z-index: 100;
            background: #333;
            box-shadow: 0 1px 2px 2px #333;
            border:none;
        }
        .nav-item{
            list-style: none;
            height: 20px;
            float: left;
            margin: 5px 8px;
            line-height: 20px;
            position: relative;
            text-align: center;
            cursor: pointer;
            margin-bottom: 4px;
            background-color: #F0F0F0;
            border-radius: 3px;
            padding: 2px 4px;
            border-bottom: 1px solid #000;
        }
        .nav-item a{
            display: block;
            text-decoration: none;
            color: #000;
        }
        a.router-link-active{
            color: #008000;
            font-weight: bold;
        }
        a.router-link-active:before{
            display: none;
        }
        .nav-item a:hover{
            background-color: #F0F0F0;
            cursor: pointer;
            color: #008000;
        }
        .fade-enter-active, .fade-leave-active {
          transition: opacity .3s;
        }
        .fade-enter, .fade-leave-active {
          opacity: 0;
        }
    }
</style>



