<template>
  <div id="app">
    <Headers></Headers>
    <NavBar v-if="canShow"></NavBar>
    <router-view class="main_content"></router-view>
  </div>
</template>

<script>
import NavBar from './components/nav.vue'
import Headers from './components/header.vue'

export default {
    name: 'app',
    components: {
        NavBar,
        Headers
    },
    data() {
        return {
            canShow: true
        }
    },
    computed: {
        canShow () {
            if((navigator.userAgent.match(/(phone|pad|pod|iPhone|iPod|ios|iPad|Android|Mobile|BlackBerry|IEMobile|MQQBrowser|JUC|Fennec|wOSBrowser|BrowserNG|WebOS|Symbian|Windows Phone)/i
            || document.body.clientWidth <= 1080))){
                return this.$store.state.isShow.isShow;
            }else {
                return true
            }
        }
    }
}
</script>

<style>
    body,html,h3,ul,li,section,p,div{
        margin: 0;
        padding: 0;
    }
    html,body{
        height: 100%;
    }
    body{
        font-family: "Microsoft YaHei";
        font-size: 14px;
        /*background-color: #F0F0F0;*/
    }
    .header{
        height: 80px;
        width: 100%;
        box-sizing: border-box;
        line-height: 80px;
        color: #E9F6FD;
        padding-left: 80px;
        text-align: left;
        background-color: #31AAE8;
        position: relative;
    }
    .header img{
        display: block;
        width: 48px;
        height: 48px;
        position: absolute;
        top: 50%;
        margin-top: -24px;
        left: 20px;
        z-index: 200;
    }
    .btn{
        display: none;
        position: absolute;
        top: 10px;
        right: 15px;
        width: 20px;
        height: 20px;
        background-color: #c33;
        border-radius: 50%;
    }
    #app {
      font-family: 'Avenir', Helvetica, Arial, sans-serif;
      -webkit-font-smoothing: antialiased;
      -moz-osx-font-smoothing: grayscale;
      text-align: center;
      color: #2c3e50;
    }
    .main_content{
        position: relative;
        top: 20px;
        width: 80%;
        margin-left: 260px;
        box-sizing: border-box;
    }
    .main-mod-con{
        list-style: none;
        float: left;
        width: auto;
        text-align: center;
        border-radius: 5px;
        border: 1px solid #0e90d2;
        transition: .4s all;
        margin: 6px 6px;
    }
    .main-mod-con a{
        display: block;
        padding: 10px 15px;
        color: #333;
        margin: 0 5px;
        text-decoration: none;
    }
    .main-mod-con:hover{
        box-shadow: 0 0 6px 1px #0e90d2;
        transform: translateY(-2px);
    }
    .design{
        font-size: 14px;
        position: relative;
        padding-left: 8px;
        color: #10A0EA;
        font-weight: bold;
        text-align: left;
    }
    .design:before{
        position: absolute;
        left: 0;
        bottom: 0;
        display: block;
        content: '';
        width: 4px;
        height: 16px;
        background-color: #10A0EA;
    }
    .main-mod{
        /*min-height: 150px;*/
        width: 100%;
        padding: 14px 10px 0;
        -webkit-box-sizing: border-box;
        box-sizing: border-box;
        margin-bottom: 15px;
    }
    .main-wraper{
        margin-top: 15px;
    }
    .main-wraper:after{
        content: '';
        display: block;
        overflow: hidden;
        clear: both;
    }
    @media screen and (max-width: 1080px){
        .header{
            width: 100%;
            height: 40px;
            line-height: 40px;
            position: relative;
            text-align: center;
            border-bottom: 1px solid #ececec;
            background-color: #31AAE8;
        }
        .btn{
            display: inline-block;
        }
        .main_content{
            margin-left: 0;
            width: 100%;
            top: 0;
        }
        .header img{
            display: none !important;
        }
        .header{
            padding-left: 20px;
            text-align: left;
        }
        .main-mod-con{
            list-style: none;
            float: left;
            width: auto;
            text-align: center;
            border-radius: 5px;
            border: 1px solid #0e90d2;
            transition: .4s all;
        }
        .main-mod-con a{
            display: block;
            padding: 6px 8px;
            color: #333;
            margin: 0 5px;
            text-decoration: none;
        }
    }
</style>



