<template>
    <div>
        <div class="header">
            <img src="../../static/img/logo.png" alt="">
            <h3>前端实用工具</h3>
            <span class="btn" @click="toShow"></span>
        </div>
    </div>
</template>

<script>
    import { mapActions } from 'vuex';
    export default {
        data () {
            return {

            }
        },
        methods: {
            ...mapActions(['enableshow','disableshow']),
            toShow () {
                this.$store.state.isShow.isShow === false ? this.enableshow() : this.disableshow()
            }
        }
    }
</script>
<style scoped>

</style>



